@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
.classroom-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
    gap: 20px;
    padding: 20px;
  }
  
  .classroom-card {
    background: white;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease-in-out;
  }
  
  .classroom-card:hover {
    transform: translateY(-5px);
  }
  
  .classroom-image {
    width: 100%;
    height: 150px;
    object-fit: cover;
  }
  
  .classroom-content {
    padding: 15px;
  }
  
  .classroom-content h3 {
    margin: 0;
    font-size: 1.2em;
  }
  
  .classroom-content p {
    margin: 5px 0;
    color: #555;
  }
  
  .button-group {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }
  
  .view-button, .edit-button, .delete-button {
    flex: 1 1;
    margin: 5px;
    padding: 8px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .view-button {
    background-color: #2196F3;
    color: white;
  }
  
  .edit-button {
    background-color: #4CAF50;
    color: white;
  }
  
  .delete-button {
    background-color: #F44336;
    color: white;
  }
  
  .no-classroom-message {
    text-align: center;
    font-size: 1.1em;
    color: #777;
  }
  
/* Main Dashboard Style */
.dashboard-container {
  padding: 50px; /* Increased padding for better spacing */
  max-width: 1600px; /* Increased size */
  margin: 50px auto; /* Increased margin for better spacing */
  background-color: #ffffff; /* Light theme background */
  border-radius: 16px; /* Increased border-radius for a modern look */
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1); /* Increased shadow for a modern look */
  width: 95%; /* Ensure it uses 95% of the available width */
}

/* Profile Card Style */
.profile-card {
  display: flex;
  align-items: center;
  background: #ffffff;
  padding: 30px; /* Increased padding */
  border-radius: 16px; /* Increased border-radius */
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1); /* Increased shadow */
  margin-bottom: 30px; /* Increased margin-bottom */
  transition: 0.3s ease;
}

.profile-card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.15); /* Increased shadow */
}

.profile-card img {
  width: 90px; /* Increased size */
  height: 90px; /* Increased size */
  border-radius: 50%;
  border: 4px solid #007bff; /* Increased border width */
  margin-right: 25px; /* Increased margin-right */
}

.profile-card h2 {
  font-size: 24px; /* Increased font size */
  font-weight: bold;
  color: #333;
  margin-bottom: 10px; /* Increased margin-bottom */
}

.profile-card p {
  color: #666; /* Slightly darker color */
}

.profile-card button {
  background: #007bff;
  color: white;
  padding: 15px 20px; /* Increased padding */
  border: none;
  border-radius: 10px; /* Increased border-radius */
  cursor: pointer;
  transition: 0.3s;
  margin-left: auto;
}

.profile-card button:hover {
  background: #0056b3;
}

/* Form Container Style */
.form-container {
  background: #ffffff;
  padding: 50px; /* Increased padding */
  border-radius: 16px; /* Increased border-radius */
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1); /* Increased shadow */
  margin-bottom: 30px; /* Increased margin-bottom */
  transition: 0.3s ease;
}

.form-container:hover {
  transform: translateY(-5px);
  box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.15); /* Increased shadow */
}

.form-container h2 {
  font-size: 28px; /* Increased font size */
  font-weight: bold;
  color: #333;
  margin-bottom: 25px; /* Increased margin-bottom */
}

.form-container input {
  width: 100%;
  padding: 20px; /* Increased padding */
  margin: 15px 0; /* Increased margin */
  border: 1px solid #ccc;
  border-radius: 10px; /* Increased border-radius */
  font-size: 18px; /* Increased font size */
  transition: 0.3s;
}

.form-container input:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/* Create Classroom Button */
.create-button {
  background: #007bff;
  color: white;
  padding: 20px; /* Increased padding */
  border: none;
  border-radius: 10px; /* Increased border-radius */
  cursor: pointer;
  width: 100%;
  font-size: 18px; /* Increased font size */
  transition: 0.3s;
}

.create-button:hover {
  background: #0056b3;
}

.create-button:active {
  background: #0056b3;
  transform: translateY(2px);
}

