  background: #dc3545;
  color: white;
}

.delete-button:hover {
  background: #c82333;
}

.delete-button:active {
  background: #bd2130;
  transform: translateY(2px);
}

.logout-button {
  background: #dc3545;
  color: white;
  width: 100%;
  padding: 20px; /* Increased padding */
  border: none;
  border-radius: 10px; /* Increased border-radius */
  cursor: pointer;
  font-size: 18px; /* Increased font size */
  transition: 0.3s;
  margin-top: 25px; /* Increased margin-top */
}

/* Update and Cancel Buttons */
.update-button, .cancel-button {
  flex: 1 1 30%; /* Ensure all buttons have the same size */
  padding: 20px; /* Increased padding */
  border-radius: 10px; /* Increased border-radius */
  font-size: 18px; /* Increased font size */
  cursor: pointer;
  transition: 0.3s;
}

.update-button {
  background: #28a745;
  color: white;
  padding: 20px; /* Increased padding */
  border: none;
  border-radius: 10px; /* Increased border-radius */
  cursor: pointer;
  width: 100%;
  font-size: 18px; /* Increased font size */
  transition: 0.3s;
}

.update-button:hover {
  background: #218838;
}

.update-button:active {
  background: #1e7e34;
  transform: translateY(2px);
}

.cancel-button {
  background: #dc3545;
  color: white;
}

.cancel-button:hover {
  background: #c82333;
}

.cancel-button:active {
  background: #bd2130;
  transform: translateY(2px);
}

/* Logout Button Hover and Active States */
.logout-button:hover {
