}

.student-list h2 {
  text-align: center;
  font-size: 24px; /* Slightly increased font size */
  margin-bottom: 20px; /* Increased margin-bottom */
}

.student-list ul {
  list-style: none;
  padding: 0;
}

.student-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  padding: 15px; /* Increased padding */
