}

/* ✅ ปุ่มสร้างรอบเช็คชื่อให้กลับมามีสี */
.btn-new {
  background-color: #28a745;
  color: white;
  font-size: 18px;
  padding: 14px;
  border-radius: 10px;
  cursor: pointer;
  border: none;
  width: 250px;
  transition: 0.3s ease-in-out;
}

.btn-new:hover {
  opacity: 0.85;
}

/* ✅ ปรับดีไซน์ของรายการเช็คชื่อ */
.checkin-history-list {
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

/* ✅ กล่องรอบเช็คชื่อ */
.checkin-card {
  padding: 15px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  transition: 0.3s ease-in-out;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
}

/* ✅ เปลี่ยนสีของกล่องเมื่อเปิด-ปิด */
.card-open {
  background-color: #c3f7c3 !important;
  border-left: 6px solid #28a745;
}

.card-closed {
  background-color: #f8c1c1 !important;
  border-left: 6px solid #dc3545;
}

/* ✅ ส่วนหัวของกล่องรอบเช็คชื่อ */
.checkin-header {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}

/* ✅ ปรับดีไซน์ของปุ่ม */
.checkin-body {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
}

/* ✅ ปรับการเรียงปุ่มให้เหมือนในภาพ */
.checkin-buttons {
  display: grid;
  grid-template-columns: repeat(6, 1fr); /* ✅ เรียงเป็น 6 คอลัมน์ */
  grid-gap: 10px;
  gap: 10px;
  width: 100%;
}

