/* ==================== */
/* Design System Variables */
/* ==================== */
:root {
  /* Color palette */
  --primary: #4361ee;
  --primary-hover: #3a56d4;
  --secondary: #4cc9f0;
  --accent: #f72585;
  --success: #2ecc71;
  --warning: #f39c12;
  --danger: #e74c3c;
  --light: #f8f9fa;
  --dark: #212529;
  
  /* Neutral colors */
  --gray-100: #f8f9fa;
  --gray-200: #e9ecef;
  --gray-300: #dee2e6;
  --gray-400: #ced4da;
  --gray-500: #adb5bd;
  --gray-600: #6c757d;
  --gray-700: #495057;
  --gray-800: #343a40;
  --gray-900: #212529;
  
  /* Typography */
  --font-family: 'Poppins', sans-serif;
  --font-size-sm: 1rem; /* Increased font size */
  --font-size-md: 1.25rem; /* Increased font size */
  --font-size-lg: 1.5rem; /* Increased font size */
  --font-size-xl: 1.75rem; /* Increased font size */
  --font-size-2xl: 2rem; /* Increased font size */
  --font-size-3xl: 2.5rem; /* Increased font size */
  
  /* Spacing */
  --space-1: 0.5rem; /* Increased spacing */
  --space-2: 1rem; /* Increased spacing */
  --space-3: 1.5rem; /* Increased spacing */
  --space-4: 2rem; /* Increased spacing */
  --space-5: 2.5rem; /* Increased spacing */
  --space-6: 3rem; /* Increased spacing */
  --space-8: 4rem; /* Increased spacing */
  --space-10: 5rem; /* Increased spacing */
  
  /* Layout */
  --border-radius-sm: 8px; /* Increased border radius */
  --border-radius-md: 12px; /* Increased border radius */
  --border-radius-lg: 16px; /* Increased border radius */
  --border-radius-xl: 20px; /* Increased border radius */
  
  /* Effects */
  --shadow-sm: 0 2px 6px rgba(0, 0, 0, 0.1); /* Increased shadow */
  --shadow-md: 0 6px 12px rgba(0, 0, 0, 0.1), 0 4px 8px rgba(0, 0, 0, 0.05); /* Increased shadow */
  --shadow-lg: 0 12px 18px rgba(0, 0, 0, 0.1), 0 8px 12px rgba(0, 0, 0, 0.05); /* Increased shadow */
  --transition-fast: 150ms ease;
  --transition-normal: 250ms ease;
}

/* ==================== */
/* Reset & Base Styles */
/* ==================== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-family);
  background: linear-gradient(135deg, #f5f7fa 0%, #e4e7eb 100%);
  color: var(--gray-800);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1.5;
}

/* ==================== */
/* Layout Components */
/* ==================== */
.login-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: var(--space-6);
  min-height: 100vh;
}

.login-box {
  background: white;
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-lg);
  width: 100%;
  max-width: 800px; /* Increased max-width */
  padding: var(--space-10);
  transition: transform var(--transition-normal);
  position: relative;
  overflow: hidden;
}

.login-box::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 8px; /* Increased height */
  background: var(--primary);
}

/* ==================== */
/* Typography */
/* ==================== */
.login-title {
  font-size: var(--font-size-3xl);
  font-weight: 600;
  color: var(--gray-900);
  margin-bottom: var(--space-8);
  text-align: center;
}

.footer-text {
  font-size: var(--font-size-md);
  color: var(--gray-600);
  text-align: center;
  margin-top: var(--space-8);
}

.footer-link {
  color: var(--primary);
  text-decoration: none;
  font-weight: 500;
  transition: color var(--transition-fast);
}

.footer-link:hover {
  color: var(--primary-hover);
  text-decoration: underline;
}

/* ==================== */
/* Form Elements */
/* ==================== */
.input-group {
  margin-bottom: var(--space-6);
}

.input-label {
  display: block;
  font-size: var(--font-size-md);
  font-weight: 500;
  color: var(--gray-700);
  margin-bottom: var(--space-3);
}

.login-input {
  width: 100%;
  font-size: var(--font-size-lg);
  padding: var(--space-4);
  border: 1px solid var(--gray-300);
  border-radius: var(--border-radius-md);
  background-color: white;
  color: var(--gray-800);
  transition: all var(--transition-normal);
}

.login-input::placeholder {
  color: var(--gray-500);
}

.login-input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(67, 97, 238, 0.15);
}

/* ==================== */
/* Buttons */
/* ==================== */
.login-button {
  width: 100%;
  background-color: var(--primary);
  color: white;
  font-size: var(--font-size-lg);
  font-weight: 500;
  padding: var(--space-4);
  border: none;
  border-radius: var(--border-radius-md);
  cursor: pointer;
  transition: all var(--transition-normal);
}

.login-button:hover {
  background-color: var(--primary-hover);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.login-button:active {
  transform: translateY(0);
}

.google-button {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: var(--space-3);
  gap: var(--space-3);
  background-color: white;
  color: var(--gray-800);
  font-size: var(--font-size-lg);
  font-weight: 500;
  padding: var(--space-4);
  border: 1px solid var(--gray-300);
  border-radius: var(--border-radius-md);
  cursor: pointer;
  margin-top: var(--space-4);
  transition: all var(--transition-normal);
}

.google-button:hover {
  background-color: var(--gray-100);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.google-button:active {
  transform: translateY(0);
}

/* ==================== */
/* Utility Components */
/* ==================== */
.divider {
  display: flex;
  align-items: center;
  text-align: center;
  color: var(--gray-500);
  font-size: var(--font-size-md);
  margin: var(--space-6) 0;
}

.divider::before,
.divider::after {
  content: '';
  flex: 1 1;
  border-bottom: 1px solid var(--gray-300);
}

.divider span {
  padding: 0 var(--space-3);
}

.login-error {
  background-color: rgba(231, 76, 60, 0.1);
  color: var(--danger);
  border-left: 3px solid var(--danger);
  border-radius: var(--border-radius-sm);
  padding: var(--space-3) var(--space-4);
  margin-bottom: var(--space-6);
  font-size: var(--font-size-md);
  text-align: left;
}

/* ==================== */
/* Responsive Adjustments */
/* ==================== */
@media (max-width: 640px) {
  .login-box {
    padding: var(--space-8); /* Increased padding */
    max-width: 100%;
  }
  
  .login-title {
    font-size: var(--font-size-2xl);
    margin-bottom: var(--space-8); /* Increased margin-bottom */
  }
}
